@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;1,200;1,300&display=swap);
*{
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  font-family: "Source Code Pro";
  background-color: #11121b;
  color: #f1f1f1;
}

.coin-app{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-search{
  margin: 40px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-text{
  padding: 40px 0px;
}

.main-text{
  padding: 20px 0px;
  font-size: 40px;
}

.coin-input{
  padding: 10px;
  background-color: white;
  width: 230px;
  height: 45px;
  border-radius: 10px;
  background-image: linear-gradient(
    -225deg,#ac32e4 0%,
    #7918f2 50%,
    #4801ff 100%
  );
  border: none;
  color: white;
}

.coin-input::-webkit-input-placeholder{
  color: white;
}

.coin-input:-ms-input-placeholder{
  color: white;
}

.coin-input::placeholder{
  color: white;
}

.coin-container{
    display: flex;
    justify-content: center;

}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    width: 1000px;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 10px;
    min-width: 300px;

}

.coin-data{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

}

.coin img{
    height: 45px;
    width: 45px;
    margin-right: 10px;

}

.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-price{
    width: 180px;
}

.coin-volume{
    width: 150px;
}

.coin-marketcap{
    width: 400px;
}

.coin-percent{
    width: 30%;
    text-align: center;
}

.red{
    color:red
}

.green{
    color: rgb(20, 155, 20);
}
